<template>
  <div class="vue-tabs" :class="{ 'stacked': isStacked }">
    <div :class="{'nav-tabs-navigation': !this.isStacked, 'left-vertical-tabs': this.isStacked }">
      <div class="nav-tabs-wrapper" :class="{ 'stacked': isStacked }">
        <ul :class="classList" role="tablist">

          <li
            v-for="(tab, index) in tabs"
            :key="tab.title"
            role="tab"
            class="tab"
            :class="{ 'active': activeTabIndex === index , 'disabled': tab.disabled }"
            :id="`t-${tab.tabId}`"
            :aria-selected="activeTabIndex === index"
            :aria-controls="`p-${tab.tabId}`"
            @click="navigateToTab(index, tab.route)"
          >
            <i v-if="tab.icon" :class=`${tab.icon}`></i>
            <span :style="getTitleStyles(index, tab)">{{ tab.title }}</span>
          </li>

        </ul>
      </div>
    </div>
    <div class='tab-content' :class="{ 'right-text-tabs': this.isStacked }">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'vue-tabs',
    props: {
      activeTabColor: String,
      activeTextColor: String,
      disabledColor: String,
      disabledTextColor: String,
      /**
       * Tab title position: center | bottom | top
       */
      textPosition: {
        type: String,
        default: 'center'
      },
      /**
       * Tab type: tabs | pills
       */
      type: {
        type: String,
        default: 'tabs'
      },
      direction: {
        type: String,
        default: 'horizontal'
      },
      /**
       * Centers the tabs and makes the container div full width
       */
      centered: Boolean,
      value: [String, Number, Object]
    },
    data () {
      return {
        activeTabIndex: 0,
        tabs: []
      }
    },
    computed: {
      isTabShape () {
        return this.type === 'tabs'
      },
      isStacked () {
        return this.direction === 'vertical'
      },
      classList () {
        let navType = this.isTabShape ? 'nav-tabs' : 'nav-pills'
        let centerClass = this.centered ? 'nav-justified' : ''
        let isStacked = this.isStacked ? 'nav-stacked' : ''
        return `nav ${navType} ${centerClass} ${isStacked}`
      },
      stackedClass () {
        return this.isStacked ? 'stacked' : ''
      },
      activeTabStyle () {
        return {
          backgroundColor: this.activeTabColor,
          color: this.activeTextColor
        }
      }
    },
    watch: {
      tabs (newList) {
        if (newList.length > 0 && !this.value) {
          if (newList.length <= this.activeTabIndex) {
            this.activateTab(this.activeTabIndex - 1)
          } else {
            this.activateTab(this.activeTabIndex)
          }
        }
        if (newList.length > 0 && this.value) {
          this.findTabAndActivate(this.value)
        }
      },
      value (newVal) {
        this.findTabAndActivate(newVal)
      }
    },
    methods: {
      navigateToTab (index, route) {
        this.changeTab(this.activeTabIndex, index, route)
      },
      activateTab (index) {
        this.activeTabIndex = index
        let tab = this.tabs[index]
        tab.active = true
        this.$emit('input', tab.title)
      },
      changeTab (oldIndex, newIndex) {
        let oldTab = this.tabs[oldIndex] || {}
        let newTab = this.tabs[newIndex]
        if (newTab.disabled) return
        this.activeTabIndex = newIndex
        oldTab.active = false
        newTab.active = true
        this.$emit('input', this.tabs[newIndex].title)
        this.$emit('tab-change', newIndex, newTab, oldTab)
      },
      addTab (item) {
        const index = this.$slots.default.indexOf(item.$vnode)
        this.tabs.splice(index, 0, item)
      },
      removeTab (item) {
        const tabs = this.tabs
        const index = tabs.indexOf(item)
        if (index > -1) {
          tabs.splice(index, 1)
        }
      },
      getTabs () {
        if (this.$slots.default) {
          return this.$slots.default.filter(comp => comp.componentOptions)
        }
        return []
      },
      findTabAndActivate (tabNameOrIndex) {
        let indexToActivate = this.tabs.findIndex((tab, index) => tab.title === tabNameOrIndex || index === tabNameOrIndex)
        // if somehow activeTabIndex is not reflected in the actual vue-tab instance, set it.
        if (indexToActivate === this.activeTabIndex && !this.tabs[this.activeTabIndex].active) {
          this.tabs[this.activeTabIndex].active = true
        }
        if (indexToActivate !== -1) {
          this.changeTab(this.activeTabIndex, indexToActivate)
        } else {
          this.changeTab(this.activeTabIndex, 0)
        }
      },
      getTitleStyles (index, tab) {
        if (this.tabs.length === 0) {
          return {}
        }
        let { active, disabled } = tab
        if (!active) {
          return {}
        }
        if (disabled) {
          return {
            backgroundColor: this.disabledColor,
            color: this.disabledTextColor
          }
        }
        return {
          backgroundColor: this.activeTabColor,
          color: (tab.position === 'center') ? this.activeTextColor : this.activeTabColor
        }
      }
    }
  }
</script>
